.search-bar {
  height: auto;
  width: 65vw;
  flex-direction: column;
  align-items: center;
  display: flex;
  position: absolute;
  top: 4vw;
  right: 9vw;
}

#search-form {
  width: 55vw;
  height: auto;
  background: linear-gradient(135deg, rgba(236, 239, 240, .5) 0%, rgba(51, 61, 102, .5) 100%);
  border-radius: 2vw;
  align-items: center;
  padding: .4vw;
  transition: all .3s;
  display: flex;
  box-shadow: 0 .3vw .8vw rgba(0, 0, 0, .2);
}

#city-input {
  width: 100%;
  height: 1.8vw;
  background-color: #fff;
  border: none;
  border-radius: 2vw;
  outline: none;
  flex: 1;
  padding: .5em;
  font-size: 1.3vw;
  transition: all .3s;
  box-shadow: inset 0 .2vw .5vw rgba(144, 142, 142, .553);
}

#city-input:focus {
  box-shadow: 0 0 1vw rgba(70, 130, 180, .5);
}

#location-icon, #star-icon {
  width: 2.5vw;
  cursor: pointer;
  font-size: 1.6vw;
  transition: all .3s;
}

#location-icon {
  color: #313030;
  margin-left: .8vw;
}

#star-icon {
  color: #b0afaf;
  margin-left: .8vw;
}

#location-icon:hover, #star-icon:hover {
  color: #fff;
  transform: scale(1.3);
}

.position-favorite-elemet {
  align-items: center;
  justify-items: center;
  display: flex;
}

ul {
  margin: 0;
  padding: 0;
}

#favorites-list {
  height: auto;
  margin-top: 1vw;
}

#favorites-list li {
  cursor: pointer;
  color: #fffdfd;
  background-color: rgba(255, 255, 255, .3);
  border-radius: 1vw;
  margin: 0 .8vw;
  padding: .8vw;
  font-size: 1.3vw;
  list-style: none;
  transition: transform .3s, background-color .3s;
  display: inline-block;
  position: relative;
}

#favorites-list li:hover {
  background-color: rgba(255, 255, 255, .2);
  transform: scale(1.1);
}

.remove-favorite {
  color: rgba(255, 255, 255, .7);
  opacity: 0;
  font-size: 2vw;
  transition: color .3s, transform .3s, opacity .3s;
  position: absolute;
  top: 4vw;
  right: 0;
  transform: translateY(-50%);
}

#favorites-list li:hover .remove-favorite {
  opacity: 1;
  color: tomato;
}

.remove-favorite:before {
  content: "ï‡¸";
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
}

#favorites-list li:hover .remove-favorite {
  transform: rotate(0deg);
}

#show-more-btn svg, #show-less-btn svg {
  width: 3vw;
  height: 3vw;
}

#show-more-btn, #show-less-btn {
  text-align: center;
  width: 3vw;
  height: 3vw;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  align-items: center;
  justify-items: center;
  padding: .5vw;
  font-size: 2vw;
  transition: all .3s;
  display: flex;
}

#show-more-btn:hover, #show-less-btn:hover {
  transform: scale(1.3);
}

.show-more-icon:before, .show-less-icon:before {
  color: #625f5f;
  font-family: "Font Awesome 5 Free";
  font-size: 2.5vw;
  font-weight: 900;
}

#show-more-btn:hover, #show-less-btn:hover {
  transform: scale(1.3);
}

.modal {
  z-index: 999;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-content {
  width: 80%;
  max-width: 400px;
  text-align: center;
  background-color: #fff;
  border-radius: 8px;
  margin: 15% auto;
  padding: 20px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, .3);
}

.modal-content p {
  color: #333;
  font-size: 18px;
}

.close {
  color: #aaa;
  float: right;
  cursor: pointer;
  font-size: 28px;
  font-weight: bold;
}

.close:hover, .close:focus {
  color: #000;
  cursor: pointer;
  text-decoration: none;
}

.lang-btn {
  cursor: pointer;
  background-color: #ddd;
  border: none;
  border-radius: 5px;
  margin: 10px;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
}

.lang-btn.active {
  color: #fff;
  background-color: #333;
}

.lang-btn:hover {
  color: #fff;
  background-color: #555;
}

.modal-buttons {
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

@media screen and (max-width: 500px) {
  .search-bar {
    width: 90vw;
    top: 20vw;
    right: 5vw;
  }

  #search-form {
    width: 85vw;
  }

  #city-input {
    height: 3vw;
    font-size: 2vw;
  }

  #location-icon, #star-icon, #favorites-list li {
    font-size: 2vw;
  }

  #show-more-btn, #show-less-btn {
    width: 3vw;
    height: 3vw;
    font-size: 1.5vw;
  }
}

@media screen and (max-width: 200px) {
  .search-bar {
    width: 90vw;
    top: 20vw;
    right: 5vw;
  }

  #search-form {
    width: 95vw;
    padding: 1vw;
  }

  #city-input {
    height: 3vw;
    padding: .5em;
    font-size: 2.5vw;
  }

  #location-icon, #star-icon, #favorites-list li {
    font-size: 2.5vw;
  }

  #show-more-btn svg, #show-less-btn svg {
    width: 4vw;
    height: 4vw;
  }
}

h1, h2, h3, h4, h5, h6, p {
  cursor: default;
}

h2 {
  margin: 0;
}

.master-weather-card {
  width: 35vw;
  height: 25vw;
  flex-direction: column;
  align-items: flex-end;
  position: absolute;
  top: 10vw;
  right: 0;
  display: flex !important;
}

.weather-card {
  width: 32vw;
  height: 20vw;
  background-color: #102136;
  border-radius: 3vw 0 0 3vw;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  box-shadow: 0 .3vw .5vw rgba(16, 33, 54, .59);
}

.city-name {
  color: rgba(255, 255, 255, .61);
  justify-content: center;
  margin-bottom: 0;
  font-size: 1.2vw;
  line-height: 1.375;
  display: flex;
}

.temperature-card {
  font-family: var(--font-family);
  font-weight: 1vw;
  letter-spacing: .05vw;
  text-align: center;
  color: #fff;
  margin: 0;
  font-size: 6vw;
}

.description {
  justify-content: center;
  margin-top: 1vw;
  font-size: 1vw;
  display: flex;
}

.description img {
  width: 2vw;
}

.min-temp, .max-temp, .humidity {
  color: rgba(255, 255, 255, .3);
  justify-content: center;
  display: flex;
}

.min-temp, .max-temp {
  color: #fcfcfc;
  margin-bottom: 0;
  font-size: 1.5vw;
}

.min-text, .max-text {
  color: rgba(255, 255, 255, .3);
  flex-direction: column;
  align-items: center;
  font-size: 1.2vw;
  display: flex;
}

.min, .max {
  padding: 0 1vw;
  display: flex;
}

.min {
  border-right: .1vw solid rgba(255, 255, 255, .3);
}

.min-max-container {
  justify-content: center;
  gap: 20px;
  display: flex;
}

.temps-divide {
  justify-content: center;
  display: flex;
}

.divide-line {
  height: 3vw;
  margin: 0 1vw;
}

.button-container {
  flex-direction: row;
  position: absolute;
  top: 2vw;
  right: 0;
  display: flex !important;
}

.today-button {
  margin-right: 1vw;
}

.five-day-button {
  margin-right: 0;
}

.btns-today, .weather-button {
  text-transform: uppercase;
  width: 15vw;
  height: 2vw;
  font-family: var(--font-family);
  text-align: center;
  color: #000;
  background-color: rgba(255, 255, 255, .506);
  border: none;
  border-radius: 3vw;
  margin-top: 0;
  padding: 0;
  font-size: 1.4vw;
  font-weight: 700;
}

.weather-button:focus {
  background-color: #fff;
}

#forecast-location {
  font-family: var(--font-family);
  letter-spacing: .04em;
  text-align: center;
  color: #fff;
  font-size: 2vw;
  font-weight: 700;
}

#forecast-container {
  width: 90vw;
  height: auto;
  background: rgba(16, 33, 54, .8);
  border-radius: 3vw;
  flex-flow: wrap;
  justify-content: center;
  align-items: center;
  padding: 4vw;
  box-shadow: 0 1vw 2vw rgba(16, 33, 54, .6);
}

.five-days-container {
  width: 90vw;
  flex-direction: column;
  align-items: center;
  display: flex;
  position: absolute;
  top: 20vh;
  right: 5vw;
}

.forecast-day h3, .forecast-day p {
  margin: .2vw 0;
}

.forecast-day, .date-week {
  font-family: var(--font-family);
  text-align: center;
  color: #fff;
  font-size: 2vw;
  font-weight: 400;
}

.forecast-day .date {
  text-align: center;
  color: #fff;
  opacity: .55;
  box-sizing: border-box;
  flex: 0 18%;
  font-size: 1.5vw;
}

.forecast-day .temp {
  font-size: 2vw;
}

.forecast-day .temp-min {
  border-right: .1vw solid rgba(255, 255, 255, .3);
  padding-right: 10px;
}

.forecast-day .temp-max {
  padding-left: 10px;
}

.forecast-day .temp-min, .forecast-day .temp-max {
  color: #ff6b09;
  flex-direction: column;
  font-size: 2vw;
  font-weight: 700;
  display: flex;
}

.forecast-day img {
  width: 3vw;
}

.five-days-forecast {
  justify-content: center;
  display: flex;
}

.five-days-min, .five-days-max {
  text-align: center;
  color: #fff;
  opacity: .55;
  font-size: 1.3vw;
  font-weight: 400;
}

.forecast-day .more-info {
  text-align: center;
  color: #fff;
  opacity: .3;
  cursor: pointer;
  margin-bottom: 2vw;
  font-size: 1vw;
  font-weight: 700;
  text-decoration: underline;
}

.detailed-forecast {
  width: 100%;
  overflow: hidden;
}

.detailed-forecast-carousel {
  width: 85vw;
  white-space: nowrap;
  justify-content: center;
  align-items: center;
  margin-top: 2vw;
  display: flex;
  position: relative;
  overflow-x: auto;
  overflow-y: hidden;
}

.overflow-five-days-container {
  width: 100%;
  height: auto;
  align-items: center;
  padding-bottom: 5vw;
  display: flex;
  position: relative;
  overflow: hidden;
}

.overflow-five-days {
  white-space: nowrap;
  width: 100%;
  transition: transform .3s ease-in-out;
  display: flex;
}

.arrow {
  color: #e4e1e1;
  cursor: pointer;
  z-index: 10;
  background-color: rgba(100, 100, 100, .467);
  border: none;
  border-radius: 5vw;
  padding: 8px;
  position: absolute;
  top: 90%;
  transform: translateY(-50%);
}

.arrow-left {
  right: 35vw;
}

.arrow-right {
  left: 35vw;
}

.arrow:disabled {
  opacity: .5;
}

.forecast-day {
  text-align: center;
  border: 1px solid rgba(255, 255, 255, .5);
  border-radius: 1vw;
  flex: 0 0 20%;
  margin: 0 5px;
  padding: 10px;
}

.forecast-day img {
  max-width: 100%;
  height: auto;
}

.carousel-container {
  width: 90%;
  overflow: auto;
}

.carousel-items {
  width: max-content;
  scroll-behavior: smooth;
  flex-wrap: nowrap;
  justify-content: space-between;
  gap: 4vw;
  transition: transform .3s;
  display: flex;
  overflow: auto;
}

.left-arrow, .right-arrow {
  width: 3vw;
  height: 3vw;
  color: #fff;
  cursor: pointer;
  z-index: 10;
  background-color: rgba(255, 255, 255, .2);
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: background-color .3s, box-shadow .3s, transform .3s;
  display: flex;
  position: absolute;
  top: 35vw;
  transform: translateY(-50%);
  box-shadow: 0 .4vw .8vw rgba(0, 0, 0, .3);
}

.left-arrow:hover, .right-arrow:hover {
  background-color: rgba(255, 255, 255, .5);
  transform: translateY(-50%)scale(1.1);
  box-shadow: 0 .8vw 1.6vw rgba(0, 0, 0, .4);
}

.hourly {
  text-align: center;
  color: #fff;
  opacity: .54;
  margin-bottom: 0;
  font-size: .73vw;
}

.hourly-temp {
  text-align: center;
  color: #ff6b09;
  margin: 0;
  font-size: 1.3vw;
}

.hourly-icon img {
  width: 6.25vw;
}

.hourly-pressure, .hourly-humidity, .hourly-wind {
  color: #fff;
  opacity: .54;
  font-size: .73vw;
}

.carousel-items {
  width: max-content;
  display: flex;
  overflow: hidden;
}

.hourly-forecast-card {
  width: 9vw;
  height: auto;
  text-align: center;
  border: .1vw solid rgba(255, 255, 255, .3);
  border-radius: 1.3vw;
  flex: 1 0 auto;
  display: inline-block;
}

.hourly-forecast-card:hover {
  background: rgba(255, 255, 255, .1);
}

.detailed-forecast::-webkit-scrollbar {
  height: 3px;
}

.detailed-forecast::-webkit-scrollbar-thumb {
  background-color: #fd8301;
  border-radius: 8px;
}

.detailed-forecast::-webkit-scrollbar-track {
  background-color: #ccc;
  border-radius: 4px;
}

